const CONTAINER = {
  namespaced: true,
  state: {
    origin: '',//
    destination: '',//
    selectDayList: [],//
    specifiedDate: '', //
    trainNumberList: [], //
    trainNumberStr: '', //
    seatType: '',
    passengerIdList:  [],
    passengerIdListName: '',
    todayTime: '',
    currentSeatTypeArr: [], // 
  },
  getters: {
    origin (state) {
      return state.origin
    },
    destination (state) {
      return state.destination
    },
    selectDayList (state) {
      return state.selectDayList
    },
    specifiedDate (state) {
      return state.specifiedDate
    },
    seatType (state) {
      return state.seatType
    },
    passengerIdList(state) {
      return state.passengerIdList
    },
    passengerIdListName(state) {
      return state.passengerIdListName
    },
    todayTime(state) {
      return state.todayTime
    },
    trainNumberList(state) {
      return state.trainNumberList
    },
    trainNumberStr(state) {
      return state.trainNumberStr
    },
    currentSeatTypeArr(state) {
      return state.currentSeatTypeArr
    },
  },
  mutations: {
    setOptions(state, options = {}) {
        for (let key in options) {
            state[key] = options[key];
        }
    },
  }
}
export default CONTAINER



